import React from "react";
import { Row, Col } from "antd";

const ChangePwd = React.forwardRef(({ id, title, placeholder, style }, ref) => (
  <Row
    type="flex"
    align="middle"
    justify="space-between"
    gutter={[0, { xs: 8 }]}
    className="plate_cell"
    style={style}
  >
    <Col sm={{ span: 8 }} xs={{ span: 24 }}>
      <label className="pwd_label" htmlFor={id}>
        {title}:
      </label>
    </Col>
    <Col sm={{ span: 16 }} xs={{ span: 24 }}>
      <input
        id={id}
        className="pwd_input"
        type="password"
        ref={ref}
        placeholder={placeholder}
      />
    </Col>
  </Row>
));

export default ChangePwd;
