import React, { PureComponent } from "react";
import { Row, Col, Button } from "antd";

export default class PersonalCell extends PureComponent {
  state = {};
  render() {
    const { title, btn, clickBtn, middleNode, style } = this.props;
    return (
      <Row
        type="flex"
        align="middle"
        justify="space-between"
        gutter={[0, { xs: 8 }]}
        className="plate_cell"
        style={style}
      >
        <Col sm={{ span: 5 }} xs={{ span: 24 }}>
          <span className="title_left">{title} :</span>
        </Col>
        <Col sm={{ span: 11 }} xs={{ span: 12 }}>
          {middleNode}
        </Col>
        <Col sm={{ span: 8 }} xs={{ span: 12 }} className="xs_inner">
          {clickBtn && <Button onClick={clickBtn}>{btn}</Button>}
        </Col>
      </Row>
    );
  }
}
