import React, { Component } from "react";
import { message, Icon, Tooltip, Row, Col, Avatar, Button } from "antd";
import { withTranslation } from "react-i18next";
import { inject, observer } from "mobx-react";
import { storage, reg } from "../../util/index";
import { host, wsget } from "../../ws";
import Footer from "../../common/Widget/Footer";
import "./style.scss";
import { get, observable } from "mobx";
import Headportrait from "../../components/Headportrait";
import i18next from "i18next";
import PersonalCell from "./PersonalCell";
import ChangePwd from "./ChangePwd";

@withTranslation()
@inject("login")
@observer
class Personal extends Component {
  constructor(props) {
    super(props);
    this.nameRef = React.createRef();
    this.pwdref = React.createRef();
    this.emailRef = React.createRef();

    this.old_pwdRef = React.createRef();
    this.new_pwdRef = React.createRef();

    this.state = {
      visible: false,
    };
  }
  @observable nameVisible = false;
  @observable emailVisible = false;
  @observable pwdVisible = false;

  componentDidMount() {
    const { user_info } = this.props.login;
    this.props.login.getUser_detail(() => {
      this.nameRef.current.value = get(user_info, "nickname") || get(user_info, "name");
    });

    document.addEventListener("click", this.chengeEmailHide);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.chengeEmailHide);
  }

  chengeEmailHide = (e) => {
    if (e.target.nodeName === "INPUT") {
      return;
    }
    this.emailVisible = false;
    this.nameVisible = false;
  };

  //重命名
  changeName = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    const { user_info } = this.props.login;
    if (this.nameVisible) {
      const name = this.nameRef.current.value.trim();
      if (name === "") {
        message.error(i18next.t("NicknameEmpty"));
        return;
      }
      if (name !== get(user_info, "nickname")) {
        if (!reg.nickname.test(name)) {
          message.error(i18next.t("nicknameValidity"));
          return;
        }
        wsget("user_set_nickname", name, 1).then(() => {
          this.props.login.changeInfo('nickname', name);
        });
      }
    }
    this.nameVisible = !this.nameVisible;
    this.nameRef.current.value =
      get(user_info, "nickname") || get(user_info, "name");
  };

  //更换邮箱
  changeEmail = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    const { user_info } = this.props.login;
    if (this.emailVisible) {
      const email = this.emailRef.current.value.trim();
      if (!email) {
        this.emailVisible = false;
        return;
      }
      if (reg.email.test(email)) {
        if (email !== get(user_info, "email")) {
          if (!this.props.login.checkEmail(email)) {
            wsget('user_set_email', email, 1).then(() => {
              this.props.login.changeInfo('email', email);
            })
          } else {
            message.error(i18next.t("boundmail"));
          }
        }
      } else {
        message.error(i18next.t("emailValidity"));
      }
    }
    this.emailVisible = !this.emailVisible;
    this.emailRef.current.value = get(user_info, "email") || "";
  };

  //更换头像start
  showModule = () => {
    this.setState({ visible: true });
  };
  hideModal = () => {
    this.setState({ visible: false });
  };

  //修改密码
  preserve = () => {
    const old_pwd = this.old_pwdRef.current.value.trim();
    const new_pwd = this.new_pwdRef.current.value.trim();
    let user = storage.get("user");
    if (!old_pwd) {
      message.error(i18next.t("enterOldpwd"));
      return;
    }
    if (!new_pwd) {
      message.error(i18next.t("enterset"));
      return;
    }
    if (!reg.pwd.test(new_pwd)) {
      message.error(i18next.t("pwdFormat"));
      return;
    }
    if (old_pwd !== user.pwd) {
      message.error(i18next.t("oldpwdinc"));
      return;
    }
    if (old_pwd === new_pwd) {
      message.error(i18next.t("oldnewsame"));
      return;
    }
    wsget("user_repassword", { oldpwd: old_pwd, newpwd: new_pwd }, 1).then(
      () => {
        user.pwd = new_pwd;
        storage.set("user", user);
      }
    );
    this.pwdVisible = false;
  };

  //上传头像
  upPortrait = (_, id) => {
    this.props.login.changeInfo('profile', { id })
  };

  render() {
    const { t } = this.props;
    const { nameVisible, emailVisible, pwdVisible } = this;
    const { visible } = this.state;
    const { user_info } = this.props.login;
    const ref = /(.{1}).+(.{2}@.+)/g;

    return (
      <div className="personal_main">
        <div className="personal_content">
          <div className="p-content">
            <div className="content-header">
              <h2>{t("accountmanagement")}</h2>
              <div className="plate-1">
                <div className="subhead">
                  <h4>{t("Personalinformation")}</h4>
                </div>

                <PersonalCell
                  title={t("HeadPortrait")}
                  btn={t("ChangetheAvatar")}
                  clickBtn={this.showModule}
                  middleNode={
                    get(user_info, "profile") ? (
                      <Avatar
                        size={50}
                        src={`${host()}/uf/s/${get(user_info, "profile").id
                          }/${get(user_info, "id")}`}
                      />
                    ) : (
                        <Avatar size={50} icon="user" />
                      )
                  }
                />

                <PersonalCell
                  title={t("Nickname")}
                  btn={nameVisible ? t("Determine") : t("ChangeNickname")}
                  clickBtn={this.changeName}
                  middleNode={
                    <>
                      <Tooltip
                        placement="topLeft"
                        overlayClassName="tooltip"
                        trigger="focus"
                        title={t("pwdreg")}
                      >
                        <input
                          className="middle_input"
                          style={{ display: nameVisible ? "block" : "none" }}
                          spellCheck={false}
                          type="text"
                          maxLength={50}
                          ref={this.nameRef}
                          placeholder={t("SetNickname")}
                        />
                      </Tooltip>
                      <p
                        className="staticname ellipsis"
                        style={{ display: nameVisible ? "none" : "block" }}
                      >
                        {user_info.nickname
                          ? user_info.nickname
                          : user_info.name}
                      </p>
                    </>
                  }
                />
              </div>

              <div className="plate-2">
                <div className="subhead">
                  <h4>{t("accountinformation")}</h4>
                </div>

                <PersonalCell
                  title={t("UserName")}
                  middleNode={
                    <p className="staticname ellipsis">
                      {get(user_info, "name")}
                    </p>
                  }
                />

                <PersonalCell
                  title={t("mailbox")}
                  btn={emailVisible ? t("Determine") : t("Changingmailbox")}
                  clickBtn={this.changeEmail}
                  middleNode={
                    <>
                      <input
                        className="middle_input"
                        style={{ display: emailVisible ? "block" : "none" }}
                        spellCheck={false}
                        type="email"
                        ref={this.emailRef}
                        placeholder={t("enterEmail")}
                      />
                      <p
                        className="staticname ellipsis"
                        style={{ display: emailVisible ? "none" : "block" }}
                      >
                        {get(user_info, "email")
                          ? get(user_info, "email").replace(ref, "$1****$2")
                          : t("NotMailbox")}
                      </p>
                    </>
                  }
                />

                <PersonalCell
                  title={t("Password")}
                  btn={t("Changepassword")}
                  clickBtn={() => {
                    this.pwdVisible = true;
                  }}
                  middleNode={
                    <input
                      className="middle_input pwd"
                      type="password"
                      defaultValue={"************"}
                      readOnly={true}
                    />
                  }
                  style={{ border: 0 }}
                />
              </div>
            </div>
          </div>
        </div>

        {visible && (
          <Headportrait
            userId={user_info.id}
            handleCancel={this.hideModal}
            callback={this.upPortrait}
          />
        )}

        {pwdVisible && (
          <div className="changepwd-inner">
            <div className="changepwd-content">
              <span
                className="close_icon"
                onClick={() => {
                  this.pwdVisible = false;
                }}
              >
                <Icon type="close" />
              </span>
              <div className="changepwd-head">
                <h4>{t("ChangePwd")}</h4>
              </div>
              <div className="changepwd-bd">
                <ChangePwd title={t("Oldpwd")} ref={this.old_pwdRef} placeholder={t("enterOldpwd")} />

                <ChangePwd title={t("NewPwd")} ref={this.new_pwdRef} placeholder={t("enterNewpwd")} />
              </div>
              <Row className="changepwd-ft">
                <Col sm={{ span: 16, push: 4 }} xs={{ span: 24, push: 0 }}>
                  <Button
                    className="changepwd-btn"
                    type="primary"
                    onClick={this.preserve}
                  >
                    {t("Preservation")}
                  </Button>
                </Col>
              </Row>
            </div>
          </div>
        )}

        <Footer />
      </div>
    );
  }
}
export default Personal;
