import React from 'react';
import CryptoJS from 'crypto-js';
import { Icon, message } from 'antd';
import { withTranslation } from 'react-i18next';
import i18next from 'i18next';
import { wsget, host } from '../../ws';
import { ajaxPromise, dataURLtoBlob, getCookie } from '../../util/index';
import './style.scss';
import SelectProfilePic from './SelectProfilePic';

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

@withTranslation()
class Headportrait extends React.Component {
  constructor(props) {
    super(props);
    this.imageRef = React.createRef();
    this.state = {
      imageUrl: null
    }
  }

  handleChange = (e) => {
    if (e.target.files.length > 0) {
      let file = e.target.files[0];
      if (file.size / 1024 / 1024 > 3) {
        message.error(i18next.t('Picsize'))
        return;
      }
      getBase64(file, (imageUrl) => {
        this.setState({ imageUrl, file })
      })
    }
  }

  handleCancel = () => {
    this.props.handleCancel()
  }

  handleconfirm = (cropper) => {
    this.props.handleCancel();
    const key = 'portrait';
    message.loading({ content: 'uploading...', key, duration: 0 });
    const { file } = this.state
    let afterimg = cropper.getCroppedCanvas({
      imageSmoothingQuality: 'high'
    }).toDataURL('image/jpeg');
    let wordArray = CryptoJS.lib.WordArray.create(afterimg);
    const hash = CryptoJS.SHA256(wordArray).toString();
    let blob = dataURLtoBlob(afterimg);
    ajaxPromise(blob, `${host()}/up/p/${getCookie('lovtok')}/${hash}/${file.name}`)
      .then((result) => {
        wsget('user_profile_id_save', result.id).then((res) => {
          message.success({ content: 'succeed!', key, duration: 2 });
          this.props.callback(null, result.id);
        }).catch(() => {
          message.error({ content: 'come to nothing', key, duration: 3 })
        })
      })
  }

  render() {
    const { imageUrl } = this.state;
    const { t, handleCancel } = this.props;

    return (
      <div className='Headportrait-wrap'>
        <div className='Modify_the_picture'>
          <span className='close-icon' onClick={handleCancel}>
            <Icon type="close" />
          </span>
          <div className='Modify_head'>
            {t('Headimage')}
          </div>
          <div className='Modify_content'>
            <p>{t('uploadavatar')}</p>
            <div className="Modify_openimg">
              <input type="button" className="pass-portrait-filebtn" name="openImgBtn" id="openImgBtn" value={t('Choosepicture')} />
              <input
                type="file"
                className="pass-portrait-file"
                accept="image/bmp,image/jpeg,image/png"
                name="file" id="fileImg"
                onChange={(e) => { this.handleChange(e) }} />
              <span className="pass-portrait-msg">{t('SupportPic')}</span>
            </div>

            {imageUrl && <SelectProfilePic
              img={this.state.imageUrl}
              confirm={this.handleconfirm}
              cancel={this.handleCancel} />}

          </div>
        </div >
      </div>
    );
  }
}
export default Headportrait;