import React from 'react';
import Cropper from 'cropperjs'
import { withTranslation } from 'react-i18next';
import 'cropperjs/dist/cropper.css'

@withTranslation()
class SelectProfilePic extends React.Component {
  constructor(props) {
    super(props);
    this.image = React.createRef();
    this.state = {
      myCropper: null,
      afterImg: ''
    }
  }

  componentDidMount() {
    let myCropper = new Cropper(this.image.current, {
      viewMode: 1,
      dragMode: 'none',
      initialAspectRatio: 1,
      aspectRatio: 1,
      preview: '.before',
      background: false,
      autoCropArea: 0.6,
      zoomOnWheel: false,
    })
    this.setState({
      myCropper
    }, () => {
      this.state.myCropper.replace(this.props.img)
    })
  }

  UNSAFE_componentWillReceiveProps(props) {
    const { img } = props;
    if (this.props.img !== img) {
      this.state.myCropper.replace(img)
    }
  }
  render() {
    const { t, confirm, cancel } = this.props
    return (
      <div className='preview_picture'>
        <div className='preview_left'>
          <div className="show-img">
            <img src={this.props.img} ref={this.image} alt="" />
          </div>
          <div className='preview_btn'>
            <span className='confirm' onClick={() => { confirm(this.state.myCropper) }}>{t('Determine')}</span>
            <span className='cancel' onClick={cancel}>{t('Cancel')}</span>
          </div>
        </div>
        <div className='preview_right'>
          <p>{t('imagePreview')}</p>
          <div className="before"></div>
        </div>
      </div>
    );
  }
}

export default SelectProfilePic;